<template>
  <div class="container mx-auto mt-4 p-4">
    <div class="bg-white rounded-2xl p-4 md:pt-10 md:p-16">
      <p class="font-bold text-2xl">Your rewards summary</p>
      <div class="flex-row md:flex">
        <div
          class="flex-1 shadow-md md:h-80 mt-4 rounded-2xl bg-gradient-to-r from-green-400 to-green-600 text-white flex"
        >
          <div class="flex-1 flex flex-wrap justify-center content-center">
            <div class="md:text-4xl">
              <p>Akita earned :</p>
              <p class="animate-pulse">{{ formatBN(earned) }}</p>
            </div>
          </div>
          <div class="flex-1 text-right">
            <img class="md:h-full" src="@/assets/img/bigAkita.svg" />
          </div>
        </div>
        <div class="flex-grow-0 w-5"></div>
        <div class="flex-1 shadow-md md:h-80 mt-4 rounded-2xl border-gray-400">
          <div class="p-8 md:p-10">
            <div class="float-left mr-5 h-20 md:h-10">
              <svg
                viewBox="64 64 896 896"
                focusable="false"
                data-icon="wallet"
                width="2rem"
                height="2rem"
                fill="currentColor"
              >
                <path
                  d="M880 112H144c-17.7 0-32 14.3-32 32v736c0 17.7 14.3 32 32 32h736c17.7 0 32-14.3 32-32V144c0-17.7-14.3-32-32-32zm-40 464H528V448h312v128zm0 264H184V184h656v200H496c-17.7 0-32 14.3-32 32v192c0 17.7 14.3 32 32 32h344v200zM580 512a40 40 0 1080 0 40 40 0 10-80 0z"
                ></path>
              </svg>
            </div>
            <div class="md:text-2xl">
              <p>Your Akita wallet balance :</p>
              <p>{{ formatBN(totalBalance) }}</p>
            </div>
          </div>
          <div class="p-8 pt-0 md:p-10">
            <div class="float-left mr-5 h-40">
              <svg
                viewBox="64 64 896 896"
                focusable="false"
                data-icon="lock"
                width="2em"
                height="2em"
                fill="currentColor"
              >
                <path
                  d="M832 464h-68V240c0-70.7-57.3-128-128-128H388c-70.7 0-128 57.3-128 128v224h-68c-17.7 0-32 14.3-32 32v384c0 17.7 14.3 32 32 32h640c17.7 0 32-14.3 32-32V496c0-17.7-14.3-32-32-32zM332 240c0-30.9 25.1-56 56-56h248c30.9 0 56 25.1 56 56v224H332V240zm460 600H232V536h560v304zM484 701v53c0 4.4 3.6 8 8 8h40c4.4 0 8-3.6 8-8v-53a48.01 48.01 0 10-56 0z"
                ></path>
              </svg>
            </div>
            <div class="md:text-2xl">
              <p>Locked amount :</p>
              <p>{{ formatBN(lockedAmount) }}</p>
              <p class="text-sm">
                your locked balance will be available after 1 month.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="bg-white rounded-2xl p-4 md:p-16 md:pt-10 mt-6 md:mt-10">
      <p class="font-bold text-2xl">
        {{ stakePools.length }} Available staking Opportunities
      </p>
      <PoolCard
        v-for="pool in stakePools"
        :key="pool.id"
        :pool="pool"
        :totalAllocPoint="totalAllocPoint"
        :rewardsPerBlock="rewardsPerBlock"
      />
    </div>
  </div>
</template>

<script>
import PoolCard from "@/components/PoolCard";
import { getAkitaTokenContract, getFairLaunchContract } from "@/utils/initWeb3";
import { stakePools, rewardsPerBlock } from "@/config";
import { ethers } from "ethers";
export default {
  name: "Home",
  components: {
    PoolCard,
  },
  data() {
    return {
      totalBalance: "0.00",
      lockedAmount: "0.00",
      earned: "0.00",
      totalAllocPoint: "0",
      rewardsPerBlock: "14",
      stakePools,
    };
  },
  watch: {
    "$store.state.account": function(account) {
      if (account) {
        this.load();
      }
    },
  },
  mounted() {
    if (this.$store.state.account) {
      this.load();
    }
  },
  methods: {
    async load() {
      this.totalAllocPoint = await getFairLaunchContract().totalAllocPoint();
      this.rewardsPerBlock = rewardsPerBlock;
      this.totalBalance = ethers.utils.formatEther(
        await getAkitaTokenContract().totalBalanceOf(this.$store.state.account)
      );
      this.lockedAmount = ethers.utils.formatEther(
        await getAkitaTokenContract().lockOf(this.$store.state.account)
      );
      var totalEarned = ethers.BigNumber.from("0");
      for (let i = 0; i < stakePools.length; i++) {
        const pool = stakePools[i];
        const poolEarned = await getFairLaunchContract().pendingAkita(
          pool.id,
          this.$store.state.account
        );
        totalEarned = totalEarned.add(poolEarned);
        this.earned = ethers.utils.formatEther(totalEarned);
      }
    },
  },
};
</script>
