import { createApp } from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";
import "./main.css";

const app = createApp(App);
app.mixin({
  methods: {
    formatBN(bn) {
      const strings = bn.split(".");
      if (strings.length == 1) return bn;
      return strings[0] + "." + strings[1].substring(0, 2);
    },
  },
});
app.use(store);
app.use(router);
app.mount("#app");
