import { createStore } from "vuex";

export default createStore({
  state: {
    account: "",
  },
  mutations: {
    UPDATE_ACCOUNT: function(state, account) {
      state.account = account;
    },
  },
  actions: {},
  modules: {},
  getters: {
    shortAccount(state) {
      if (state.account) {
        return (
          state.account.substring(0, 6) +
          "..." +
          state.account.substring(37, 42)
        );
      }
      return null;
    },
  },
});
