import { ethers } from "ethers";
import { fairLaunchAddress, akitaAddress } from "@/config";
import FairLaunch from "@/assets/abi/FairLaunch.json";
import AkitaToken from "@/assets/abi/AkitaToken.json";

var provider = null;
var fairLaunchContract = null;
var akitaTokenContract = null;

export function setProvider(p) {
  provider = p;
  const signer = p.getSigner();
  fairLaunchContract = new ethers.Contract(
    fairLaunchAddress,
    FairLaunch,
    provider
  ).connect(signer);
  akitaTokenContract = new ethers.Contract(
    akitaAddress,
    AkitaToken,
    provider
  ).connect(signer);
}

export function getProvider() {
  return provider;
}

export function getFairLaunchContract() {
  return fairLaunchContract;
}

export function getAkitaTokenContract() {
  return akitaTokenContract;
}
