<template>
  <div class="bg-gray-100 min-h-screen pb-10" id="app">
    <header class="container mx-auto text-gray-600 body-font">
      <div class="flex flex-wrap p-5 flex-col md:flex-row items-center">
        <a
          class="flex title-font font-bold items-center text-gray-900 mb-4 md:mb-0"
        >
          <img src="@/assets/img/logo.svg" class="w-16" />
          <span class="ml-3 text-3xl text-green-400">Akita Finance</span>
        </a>
        <nav
          class="md:ml-auto md:mr-auto text-2xl flex flex-wrap items-center text-base justify-center"
        >
          <router-link class="mr-5 hover:text-green-400" to="/"
            >Stake</router-link
          >
          <router-link class="mr-5 hover:text-green-400" to="/farm"
            >Farm</router-link
          >
          <router-link class="mr-5 hover:text-green-400" to="/earn"
            >Earn</router-link
          >
        </nav>
        <template v-if="account">
          <Button>
            {{ account }}
          </Button>
          <Button class="ml-1" @click="logout">
            Logout
          </Button>
        </template>
        <Button
          v-else
          @click="connect"
          :loading="connecting"
          class="rounded-2xl"
        >
          Connect to a Wallet
        </Button>
      </div>
    </header>
    <router-view />
  </div>
</template>

<script>
import Button from "./components/Button";
import Web3Modal from "web3modal";
import { ethers } from "ethers";
import { setProvider } from "@/utils/initWeb3.js";
import { mapGetters } from "vuex";
export default {
  components: {
    Button,
  },
  data() {
    return {
      connecting: false,
      injectProvider: null,
      web3Modal: Web3Modal,
    };
  },
  computed: {
    ...mapGetters({ account: "shortAccount" }),
  },
  mounted() {
    const providerOptions = {
      /* See Provider Options Section */
    };
    this.web3Modal = new Web3Modal({
      cacheProvider: true, // optional
      providerOptions, // required
    });
  },
  methods: {
    logout() {
      this.web3Modal.clearCachedProvider();
      this.$store.commit("UPDATE_ACCOUNT", null);
    },
    async connect() {
      try {
        this.connecting = true;
        const listenAccountsChanges = this.injectProvider == null;
        this.injectProvider = await this.web3Modal.connect();
        if (listenAccountsChanges) {
          this.injectProvider.on("accountsChanged", (accounts) => {
            console.log("accountsChanged", accounts);
            this.$store.commit("UPDATE_ACCOUNT", accounts[0]);
          });
        }
        const provider = new ethers.providers.Web3Provider(this.injectProvider);
        setProvider(provider);
        const account = await provider.getSigner().getAddress();
        this.$store.commit("UPDATE_ACCOUNT", account);
      } catch (error) {
        console.log("connect wallet", error);
      } finally {
        this.connecting = false;
      }
    },
  },
};
</script>
